<template>
	<article id="content" class="content">
		<ul class="breadcrumb">
			<li class="home"><a href="javascript:void(0);">홈</a></li>
			<li><a href="javascript:void(0);">메시지발송</a></li>
			<li><span>Thumbnail</span></li>
		</ul>
		<div class="bundle_wrap">
			<div class="emul_form_wrap">
				<div class="emul_form_head">
					<h2 class="title">메시지 발송</h2>
					<a href="#" onclick="return false" class="btn_link" @click.prevent="layerId = 'msgBox'">
						<span>메시지보관함 </span>
						<!-- 2019.10.02 툴팁추가-->
						<div class="box_tooltip">
							<p>서비스 준비중</p>
						</div>
					</a>
				</div>
				<div class="emul_form_cont">
					<div class="emul_form_type">
						<select-brand-tmplt />
					</div>

					<div class="tab_wrap" v-if="isLoadTemplate">
						<ul class="tab_nav">
							<li @click="myFilter($event)" tab-id="0" class="active"><a href="javascript:void(0);">메시지 작성</a></li>
							<li @click="myFilter($event)" tab-id="1"><a href="javascript:void(0);">발송설정</a></li>
						</ul>
						<div class="tab_container">
							<div class="tab_content active">
								<h2 class="blind">메시지작성 탭</h2>
								<div class="accordion_wrap">
									<ul class="accor_bundle_list">
										<li>
											<div class="header_accordion">
												<a href="javascript:void(0);" data-id="0" @click="accordion($event)"
													class="btn_accordion active">메인</a>
											</div>
											<div data-id="0" class="content_accordion active">
												<div class="item_bundle_wrap">
													<main-edit-set-thumbnail :boxNumber="1" :isButton="2" isImageView infoType="thumbnail"
														mainChk />
												</div>
											</div>
										</li>
										<li>
											<div class="header_accordion">
												<a href="javascript:void(0);" data-id="1" @click="accordion($event)" class="btn_accordion">상세1</a>
											</div>
											<div data-id="1" class="content_accordion">
												<div class="item_bundle_wrap">
													<main-edit-set-thumbnail :boxNumber="2" isImageView infoType="thumbnail" />
												</div>
											</div>
										</li>
										<li>
											<div class="header_accordion">
												<a href="javascript:void(0);" data-id="2" @click="accordion($event)" class="btn_accordion">상세2</a>
											</div>
											<div data-id="2" class="content_accordion">
												<div class="item_bundle_wrap">
													<main-edit-set-thumbnail :boxNumber="3" isImageView infoType="thumbnail" />
												</div>
											</div>
										</li>
										<li>
											<div class="header_accordion">
												<a href="javascript:void(0);" data-id="3" @click="accordion($event)" class="btn_accordion">상세3</a>
											</div>
											<div data-id="3" class="content_accordion">
												<div class="item_bundle_wrap">
													<main-edit-set-thumbnail :boxNumber="4" isImageView infoType="thumbnail" />
												</div>
											</div>
										</li>

										<p class="caution" style="color:red"> ※ 썸네일형(가로) 3개의 모든 상세내역 , 썸네일형(세로)는 상세 2개 이상을 적으셔야 합니다.</p>
									</ul>

									<fallback-set-box />

								</div>
							</div>
							<div class="tab_content">
								<h2 class="blind">발송설정 탭</h2>
								<div class="item_bundle_wrap">
									<receiver-set-box />
								</div>
							</div>
						</div>
					</div>

					<div class="default_guide_wrap" v-if="!isLoadTemplate">
						<reg-check-guide />
					</div>

					<div class="btn_wrap center mar_t30">
						<a href="javascript:void(0)" class="btn big point"><span>저장</span></a>
					</div>
				</div>
			</div>

			<!-- emulator -->
			<emulator-view-thumbnail pageType="thumbnail" @changeLayerId="changeLayerId" />

			<!-- 메시지보관함 팝업 -->
			<msg-store-box-layer v-if="layerId == 'msgBox'" @changeLayerId="changeLayerId" />

			<!-- 메시지 저장 팝업 -->
			<msg-store-box-save-layer v-if="layerId == 'msgBoxSave'" @changeLayerId="changeLayerId" />

			<!-- 테스트 발송 팝업 -->
			<test-sending-layer v-if="layerId == 'testSending'" @changeLayerId="changeLayerId" />

		</div>
	</article>
</template>

<script>
import moment from 'moment';
import SelectBrandTmplt from '../components/SelectBrandTmplt';
import FallbackSetBox from '../components/FallbackSetBox';
import ReceiverSetBox from '../components/ReceiverSetBox';
import MsgStoreBoxLayer from '../components/MsgStoreBoxLayer';
import MsgStoreBoxSaveLayer from '../components/MsgStoreBoxSaveLayer';
import TestSendingLayer from '../components/TestSendingLayer';
import MainEditSetThumbnail from '../components/MainEditSetThumbnail';
import EmulatorViewThumbnail from '../components/EmulatorViewThumbnail';
import RegCheckGuide from '../components/RegCheckGuide';

export default {
	name: "thumbnailSendRcsPage",
	data() {
		return {
			prevTitle: '',
			prevTitle2: '',
			prevContText: '',
			prevContText2: '',
			button01: '',
			button012: '',
			button02: '',
			button022: '',
			rejectReceiving: '',
			rejectReceiving2: '',
			rejectRadio: true,
			rejectRadio2: true,
			sendRadio: false,
			tabBoolean: true,

			layerId: '',
			isLoadTemplate: true,
			customerName: '{{고객명}}',
			numDate: '{{YYYYMMDD}}',
			blank: '{{ }}',
			fileName: '',
			selectValue: true,
			date: new Date(),
			SMSContText: '',
			LMSContText: '',
			contentPlaceHo: '변수로 설정하고자 하는 내용을 {{ }} 표시로 작성해주세요. \r\n예) {{고객명}}님 {{YYYYMMDD}} 출금예정입니다.',
			ko: vdp_translation_ko.js,
		}
	},
	components: {
		'vuejs-datepicker': vuejsDatepicker,
		SelectBrandTmplt,
		FallbackSetBox,
		ReceiverSetBox,
		MsgStoreBoxLayer,
		MsgStoreBoxSaveLayer,
		TestSendingLayer,
		MainEditSetThumbnail,
		EmulatorViewThumbnail,
		RegCheckGuide,
	},
	methods: {
		myFilter: function (event) {
			var myIndex = event.target.parentElement.getAttribute('tab-id');
			var myLi = document.querySelectorAll('.emul_form_cont .tab_nav > li');
			var myTabContent = document.querySelectorAll('.emul_form_cont .tab_container > div');
			for (var i = 0; i < myLi.length; i++) {
				myLi[i].classList.remove('active');
				myTabContent[i].classList.remove('active');
			}
			myLi[myIndex].classList.add('active');
			myTabContent[myIndex].classList.add('active');
		},
		accordion: function (event) {
			var myIndex = event.target.getAttribute('data-id');
			var element = event.target;
			var myContent = document.querySelector('.content_accordion[data-id="' + myIndex + '"]');
			for (var i = 0; i < document.querySelectorAll('.btn_accordion').length; i++) {
				document.querySelectorAll('.btn_accordion')[i].classList.remove('active');
				document.querySelectorAll('.content_accordion')[i].classList.remove('active');
			}
			event.target.classList.add('active');
			myContent.classList.add('active');
		},
		onFileChange: function (e) {
			var files = e.target.files || e.dataTransfer.files;
			if (!files.length)
				return;
			this.createImage(files[0]);
		},
		customFormatter: function (date) {
			return moment(date).format('YYYY.MM.DD');
		},
		changeLayerId(layerId) {
			this.layerId = layerId;
		},
	},
	computed: {

	}
};
</script>

